import { GET_MENU_ITEMS_START, GET_MENU_ITEMS_END, GET_MENU_ITEMS_ERROR, SET_MENU_ITEMS } from './types'
import axios from 'axios'

const instance = axios.create({
    baseURL: "http://www.mocky.io/v2",
    headers: {
        'Content-Type': 'application/json;charset=UTF-8'
    }
})

export const getMenuItems = () => {
    return dispatch => {
        dispatch({
            type: GET_MENU_ITEMS_START
        })

        return instance.get('/5be165693000002a00d9a947').then((result) => {
            const data = JSON.parse(result.data)
            dispatch({
                type: GET_MENU_ITEMS_END
            })

            if(data.success){
                dispatch({
                    type: SET_MENU_ITEMS,
                    payload: data.items
                })
            }
            else{
                dispatch({
                    type: GET_MENU_ITEMS_ERROR,
                    payload: data.error
                })
            }
        })
    }
}